.imglink{
	font-size: 1.5rem;
}
.imgbox{
	 box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
	 margin: 2rem 25rem;
	 padding: 2rem;
	 background: 
			radial-gradient(circle farthest-side at 0% 50%,#FFF 23.5%, 0)21px 30px,
			radial-gradient(circle farthest-side at 0% 50%,#FFF 24%,rgba(240,166,17,0) 0)19px 30px,
			linear-gradient(#FFF 14%, 0,  85%,#FFF 0)0 0,
			linear-gradient(150deg,#04c8de 24%,#FFF 0,#FFF 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#FFF 0,#FFF 76%,#04c8de 0)0 0,
			linear-gradient(30deg,#04c8de 24%,#FFF 0,#FFF 26%,rgba(240,166,17,0) 0,rgba(240,166,17,0) 74%,#FFF 0,#FFF 76%,#04c8de 0)0 0,
			linear-gradient(90deg,#FFF 2%,#FFF 0,#04c8de 98%,#FFF 0%)0 0 #04c8de;
			background-size: 40px 60px;;
			opacity: 0.7;
}
.input{
	width: 26rem;
	height: 2rem;
	opacity: 1;
}
.navbutton{
	background-color: black;
	color: white;
	font-size: 1.2rem;
    width: 8rem;
    height: 2.2rem;
    border-radius: 999px;
    margin: 0 1rem;
    opacity: 1;

}
.navbutton:hover{
	  transform: scale(1.1);
}