.form{
   font-size: 1.5rem;
   box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
   padding: 3rem 1rem;
   margin: 1rem 0;
}
.containerSignin{
   justify-content: center;
}
.label{
	display: block;

}
.inputsignin{
	height: 1.5rem;
    width: 15rem;
    font-size: 1rem;
    background-color: black;
    color: white;

}
.button{
	margin: 1.5rem 0;
    background-color: rgba(250, 250, 250, 0.9);
	width: 100%;
	height: 2rem;
	font-size: 1.2rem;
	border-radius: 999px;
}
.button:hover{
	 background-color: rgba(250, 250, 250, 0.5);;

}
.register{
	text-decoration: underline;
	font-size: 0.9rem;
	cursor: pointer;
}
.remember{
	font-size: 0.9rem;
}
.forgot{
	font-size: 0.8rem;
}